import './AdminProducto.css'

import { IonAlert } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useRef, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { Page, SDataTable } from '../../components'
import { cantidad_fmt } from '../../helpers'
import { useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Producto } from '../../services/api'

const numeral = require('numeral')

const defaultValue = [
  {
    cantidad_disponible: 0,
    id: 0,
  },
]

export const AdminInventario: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const DataTable = React.createRef<any>()
  const InputsRefs = useRef({})
  const [Datos, setDatos] = useState(defaultValue)
  const ValorInicial = useRef<any>({})
  const [Alert, setAlert] = useState(false)
  const [TextAlert, setTextAlert] = useState('')
  const [TitleAlert, setTitleAlert] = useState('')
  const [ProductoSeleccionado, setProductoSeleccionado] = useState<Producto>()
  const addToast = useToaster()

  const guardar = (row: any) => {
    const cantidad_disponible_inicial = ValorInicial.current[row.id].cantidad_disponible
    const cantidad_disponible_actual = Datos[row.id].cantidad_disponible

    if (ValorInicial.current[row.id].cantidad_disponible === Datos[row.id].cantidad_disponible) {
      addToast('Para guardar debe cambiar los valores', 'warning')
      return false
    }
    setAlert(true)
    setTitleAlert('Atencin')
    setTextAlert(
      'Est seguro de que desea cambiar la cantidad de  ' +
        cantidad_fmt(cantidad_disponible_inicial) +
        ' a ' +
        cantidad_fmt(cantidad_disponible_actual) +
        '?',
    )

    const cantidad_disponible = numeral(Datos[row.id].cantidad_disponible).value()
    const datos = { id: Datos[row.id].id, cantidad_disponible: cantidad_disponible }
    // @ts-ignore
    setProductoSeleccionado(datos)
  }

  const guardarDatos = () => {
    if (ProductoSeleccionado) {
      let datos = {}
      datos = {
        id: ProductoSeleccionado.id,
        cantidad_disponible: ProductoSeleccionado.cantidad_disponible,
        tipo: 'inventario'
      }
      rootStore.environment.api
        .guardarProducto(datos)
        .then((result: any) => {
          if (result.kind === 'ok') {
            addToast('Producto guardado exitosamente', 'success')
          } else {
            if (result.kind === 'bad-data') {
              if (result.errors.non_field_errors) {
                addToast(result.errors.non_field_errors[0], 'warning')
              }
            } else {
              addToast('Ocurri un error al guardar vuelva a intentar en unos momentos', 'error')
            }
          }
        })
    }
  }

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span className={'button'} title={'Visualizar'} onClick={() => guardar(row)}>
          <FIcon.Save size={14} />
        </span>
      </div>
    )
  }

  const renderInputPrecioMinorista = (row: any) => {
    return (
      <div>
        <input
          ref={(ipt) => {
            // @ts-ignore
            InputsRefs.current[row.id] = ipt
          }}
          key={'id-' + row.id.toString()}
          type={'text'}
          className={'form-control'}
          // @ts-ignore
          value={numeral(Datos[row.id].cantidad_disponible).format('0,0')}
          onChange={(evt) => {
            const datos = { ...Datos }
            const res = { ...datos[row.id] }
            // @ts-ignore
            res.cantidad_disponible = evt.target.value
            datos[row.id] = res
            setDatos(datos)
            setTimeout(() => {
              // @ts-ignore
              InputsRefs.current[row.id].focus()
            }, 100)
          }}
        />
      </div>
    )
  }

  return (
    <Page login_required admin>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TitleAlert}
        message={TextAlert}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
            handler: () => {
              setAlert(false)
            },
          },
          {
            text: 'Confirmar',
            handler: () => guardarDatos(),
          },
        ]}
      />
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Inventario</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Inventario</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                obtenerDatos={async (perPage: number, page: number, order: any, search: string) => {
                  const result = await rootStore.environment.api.productos(page, perPage, order, search, true)
                  if (result.kind === 'ok') {
                    const datos = {}
                    // @ts-ignore
                    result.data.results.map((res: any) => {
                      // @ts-ignore
                      datos[res.id] = res
                    })
                    // @ts-ignore
                    setDatos(datos)
                    // @ts-ignore
                    ValorInicial.current = datos
                  }
                  return result
                }}
                columns={[
                  {
                    name: 'Fabricante',
                    selector: 'fabricante',
                    sortable: true,
                    omit: !loginStore.administrador,
                    cell: (row: any) => row.fabricante.nombre,
                  },
                  {
                    name: 'Producto',
                    selector: 'nombre',
                    sortable: true,
                  },
                  {
                    name: 'Cantidad disponible',
                    selector: 'cantidad_disponible',
                    sortable: true,
                    cell: (row: any) => renderInputPrecioMinorista(row),
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminInventario
